import Home from './pages/Home.vue';
import Work from './pages/Work.vue';
// const Work = resolve => {     require.ensure(['./pages/Work.vue'], () => {
//      resolve(require('./pages/Work.vue'));     }) };
const Contact = resolve => {
    require.ensure(['./pages/Contact.vue'], () => {
        resolve(require('./pages/Contact.vue'));
    })
};
export const routes = [
    {
        path: '/',
        component: Home,
        name: 'home'
    }, {
        path: '/work',
        component: Work,
        name: 'work'
    }, {
        path: '/contact',
        component: Contact,
        name: 'contact'
    }, {
        path: '*', // catch wildcards
        redirect: '/'
    }
]


